const text = {
  carousel: [
    '这个问题我只问一遍，再也不提。为什么你选择的人是我？<br/>这个问题我要用一生来回答，准备好听我回答了吗？',
    '记忆的梗上，谁不有<br/>两三朵娉婷，披着情绪的花',
    '我把天空和大地打扫干干净净<br/>归还给一个陌不相识的人<br/>我寂寞地等，我阴沉地等<br/>二月的雪，二月的雨',
    '埋了一座城，关了所有灯。',
    '青灯为墙<br/>旖旎为家<br/>以梦为马<br/>不负韶华',
    '我的灯和酒坛上落满灰尘<br/>而遥远的路程上却干干净净'
  ]
}

export default text



